import React from 'react';
import PropTypes from 'prop-types';

import BasicButton from '../BasicButton';
import Desciption from '../Description';

import styles from './index.module.scss';

const ResultPage = ({img,buttonText,title,content,onClick}) => (
    <div className={styles.wrapper}>
        <div>
            <img className={styles.capture} src={img} alt="" />
        </div>
        <p className={styles.title}>{title}</p>
        {content && <Desciption content={content} className={styles.content} />}
        {buttonText && <BasicButton buttonWrapperClass={styles.button}
                                    text={Array.isArray(buttonText) ? buttonText[0].join('') : buttonText}
                                    onClick={onClick} 
                        />
        }
    </div>
);

ResultPage.propTypes = {
    img: PropTypes.string.isRequired,
    onClick: PropTypes.func,
    onFocus: PropTypes.func,
    buttonText: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
    title: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
    content: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
};

ResultPage.defaultProps = {
    onClick: () => {},
}

export default ResultPage;