import React from 'react';
import PropTypes from 'prop-types';
import LoadingIcon from '../LoadingIcon';

import styles from './index.module.scss';

const BasicButton = (props) => {
  const {text, onClick, buttonWrapperClass,
    CustomButtonIcon, CustomLoadingIcon, loading,
    disabled,
  } = props;
  return (
    <div className={`${styles.buttonWrapper} ${buttonWrapperClass} ${disabled ? styles.disabled : ''}`} onClick={onClick}>
      <div className={styles.buttonMain}>
        {text}
        <div className={`${styles.buttonIcon} ${CustomButtonIcon ? '' : styles.hide}`}>
          {
            CustomButtonIcon ? CustomButtonIcon : ''
          }
        </div>
      </div>
      <div className={`${styles.loadingIcon} ${loading ? '' : styles.hide}`}>
        {
          CustomLoadingIcon ? CustomLoadingIcon : <LoadingIcon/>
        }
      </div>
    </div>
  )
};

BasicButton.propTypes = {
  text: PropTypes.string.isRequired,
  onClick: PropTypes.func.isRequired,
  loading: PropTypes.bool,
  disabled: PropTypes.bool,
  buttonWrapperClass: PropTypes.string,
  CustomButtonIcon: PropTypes.element,
  CustomLoadingIcon: PropTypes.element,
};

BasicButton.defaultProps = {
  loading: false,
};

export default BasicButton;