const replaceTarget = (array, replaces) => {
  let sections = [];

  const lastChunk = array.reduce((acc, item) => {
    if (item === '?') {
      return [...acc, replaces.shift()];
    } else if (item === '@n') {
      sections.push(collapse(acc));
      return [];
    } else if (/@.+/.test(item)) {
      const enhancer = replaces.shift();
      if (typeof enhancer === 'function') return [...acc, enhancer(item.substring(1))];
    }
    return [...acc, item];
  }, []);
  return [...sections, lastChunk];
}

// collapse string item into a block
const collapse = (arr) => {
  const lastChuck = arr.reduce((acc, item) => {
    if (typeof item === 'string') {
      const last = acc.pop();
      if (typeof last !== 'string') {
        return [...acc, last, item];
      } else {
        return [...acc, last + item];
      }
    } else {
      return [...acc, item];
    }
  }, []);
  return lastChuck;
}
/**
 * 'a{*}c' => ['a', '*', 'c']
 */

 const spliteCopywriting = (copywriting) => {
   if (typeof copywriting !== 'string') throw new Error('copywriting must be a string!');
   
   return copywriting.split(/[{}]/);
 }

export default (copywriting, ...replaces) => {
  return replaceTarget(spliteCopywriting(copywriting), replaces);
}