import React, {Component} from 'react';
import PropTypes from 'prop-types';
import qs from 'query-string';
import {connect} from 'react-redux';

import {RESULT_PAGE_TYPE_MAP,DURATION_MAP, USER_LEVEL_MAP} from '../../config/propsPermittedValueMap';
import i18n from '../../config/i18n';
import copywritingHelper from '../../utils/copywritingHelper';
import {PREMIUM_PAGE_URL,PLUS_PAGE_URL} from '../../config/interfaceUrl';
import {descButtonClicked} from '../../ga/gaPoints';

import ResultPage from '../../components/ResultPage';

import styles from './index.module.scss';

import vipImage from '../../assets/vip.png';
import vipImage2x from '../../assets/vip@2x.png';
import vipImage3x from '../../assets/vip@3x.png';
import freeImage from '../../assets/freeonly.png';
import freeImage2x from '../../assets/freeonly@2x.png';
import freeImage3x from '../../assets/freeonly@3x.png';
import yxPointImage from '../../assets/yxpoint.png';
import yxPointImage2x from '../../assets/yxpoint@2x.png';
import yxPointImage3x from '../../assets/yxpoint@3x.png';
import activeSuccessImage from '../../assets/activesuccess.png'
import activeSuccessImage2x from '../../assets/activesuccess@2x.png'
import activeSuccessImage3x from '../../assets/activesuccess@3x.png'

class Result extends Component {
    static propTypes = {
        resultType: PropTypes.string,
        userLevel: PropTypes.string,
        activeYXPoints: PropTypes.func,
    };

    static defaultProps = {
        resultType: '',
        activeYXPoints: () => {},
    }

    constructor(props) {
        super(props);

        const {resultType, location, userLevel} = props;
        const {resultType: queryType, num, unit, userLevel: queryLevel} = qs.parse(location && location.search)
        this.state = {
            resultType: resultType || queryType, // yx-point page need to use prop to pass the type
            num,
            unit,
            userLevel: userLevel || queryLevel,
        }
        this.private = {
            copywriting: i18n[this.props.currentEnv],
        }
    }

    componentDidMount() {
        const jsBridge = window.OperateTitleBarBridge;
        if (jsBridge) {
            jsBridge.hideTitle();
            jsBridge.hideLeftBackIcon();
            jsBridge.showRightView();
        }
    }

    isHighRes = () => window.devicePixelRatio >= 1.5;
 
    isRetina = () => window.devicePixelRatio > 2;

    getProperImg = (image3x, image2x, image) => {
        return this.isRetina() ? image3x : this.isHighRes() ? image2x : image;
    }

    _resolveTime = (unit) => {
        const {YEAR,MON,DAY,PER} = this.private.copywriting; 
        return unit === DURATION_MAP.MON ? PER + MON :
               unit === DURATION_MAP.DAY ? DAY : YEAR;
    }

    _resolveUserLevel = (userLevel) => {
        const {PLUS,PREMIUM} = this.private.copywriting;
        return userLevel === USER_LEVEL_MAP.PREMIUM ? PREMIUM : PLUS;
    }

    redeemYXPoint = () => {
       this.props.activeYXPoints();
    }

    redirectToIntroPage = (userLevel) => {
        descButtonClicked(userLevel === USER_LEVEL_MAP.PREMIUM ? 2 : 1); // GA
        window.location.href = userLevel === USER_LEVEL_MAP.PREMIUM ? PREMIUM_PAGE_URL : PLUS_PAGE_URL;
        setTimeout(() => window.location.href = 'native://close', 0);
    }

    getDescriptions = () => {
        const {resultType,unit,num,userLevel} = this.state;
        const {currentEnv} = this.props;
        const {
            FREE_ONLY_TITLE,YX_POINT_TITLE,YX_POINT_DESC,
            VIP_READY_TITLE,VIP_READY_DESC,INPUT_YX_POINT,
            ACTIVE_SUCCESS_TITLE,ACTIVE_SUCCESS_DESC,INPUT_VIP_DESC,
        } = this.private.copywriting;
        const {VIP_READY,YX_POINT,FREE_ONLY,ACTIVE_SUCCESS} = RESULT_PAGE_TYPE_MAP;
        const userLevelMsg = this._resolveUserLevel(userLevel);
        let res = {}

        switch(resultType) {
            case VIP_READY:
                res = {
                    img: this.getProperImg(vipImage3x, vipImage2x, vipImage),
                    title: copywritingHelper(VIP_READY_TITLE, userLevelMsg),
                    content: copywritingHelper(VIP_READY_DESC, userLevelMsg),
                    buttonText: INPUT_YX_POINT,
                    onClick: this.redeemYXPoint,
                };
                break;
            case YX_POINT:
                res = {
                    img: this.getProperImg(yxPointImage3x, yxPointImage2x, yxPointImage),
                    title: copywritingHelper(YX_POINT_TITLE, num),
                    content: copywritingHelper(YX_POINT_DESC, (item) => <span className={styles.em}>{item}</span>),
                };
                break;
            case FREE_ONLY:
                res = {
                    img: this.getProperImg(freeImage3x, freeImage2x, freeImage),
                    title: FREE_ONLY_TITLE,
                };
                break;
            case ACTIVE_SUCCESS:
                res = {
                    img: this.getProperImg(activeSuccessImage3x, activeSuccessImage2x, activeSuccessImage),
                    title: ACTIVE_SUCCESS_TITLE,
                    content: currentEnv === 'zh_CN' ? copywritingHelper(ACTIVE_SUCCESS_DESC, `${num}${this._resolveTime(unit)}`, userLevelMsg)
                                       : copywritingHelper(ACTIVE_SUCCESS_DESC, `${num}-${this._resolveTime(unit)}`, userLevelMsg, ),
                    buttonText: copywritingHelper(INPUT_VIP_DESC, userLevelMsg),
                    onClick: () => this.redirectToIntroPage(userLevel),
                };
                break;
            default:
        }
        return res;
    }

    render() {
        return <div className={styles.wrapper}>
                 <ResultPage {...this.getDescriptions()}/>
               </div>
    }
}

const stateToProps = ({ currentEnv }) => ({ currentEnv });

export default connect(stateToProps)(Result);