export const pageActived = () => window.ga('send', 'event', 'activate', 'show_activate'); // GA-激活码页面访问量
export const activeBtnClicked = () => window.ga('send', 'event', 'activate', 'click_activate_now_btn'); // GA-激活按钮点击量
export const errorMessageShowed = () => window.ga('send', 'event', 'activate', 'show_code_error'); // GA-兑换码错误提示次数
export const invalidCode = () => window.ga('send', 'event', 'activate', 'show_code_null'); // GA-兑换码无效提示次数
export const customerServiceBtnClicked = () => window.ga('send', 'event', 'activate', 'show_help_btn'); // GA-客服按钮点击量
export const freeOnlyPageVisted = () => window.ga('send', 'event', 'activate', 'show_free_user'); // GA-仅限免费用户页面访问量
export const activeSuccessed = (value) => window.ga('send', 'event', 'activate', 'show_activate_success', transPeriodToValue(value)); // GA-激活成功页面访问量
export const descButtonClicked = (value) => window.ga('send', 'event', 'activate', 'click_vip_desciption', value); // GA-了解高级功能点击量
export const yxPointPageVisted = () => window.ga('send', 'event', 'activate', 'show_evernote_score'); // GA-兑换印象分页面访问量
export const yxPointButtonClicked = () => window.ga('send', 'event', 'activate', 'click_exchange_btn'); // GA-兑换印象分按钮点击量
export const yxPointActiveSuccessed = (value) => window.ga('send', 'event', 'activate', 'show_evernote_score', transYXPointToValue(value)); // GA-印象分兑换量统计

function transPeriodToValue (value) {
    return ['gift-3day', 'gift-7day', 'gift-14day',
        'gift-1mon', 'gift-2mon', 'gift-3mon',
        'gift-6mon', 'gift-1year', 'gift-3year',
        'plus-gift-1mon', 'plus-gift-2mon', 'plus-gift-3mon',
        'plus-gift-6mon', 'plus-gift-1year', 'plus-gift-3year'].indexOf(value) + 1;
}

function transYXPointToValue(value) {
    return [5, 10, 15, 20, 30, 60, 120, 180, 360].indexOf(value) + 1;
}