import React from 'react';
// import classnames from 'classnames';

import styles from './index.module.scss';

// const getClassName = (wrapperClass) => classnames(styles.describe, { [wrapperClass]: wrapperClass});

const resolveArrayItem = (item) => {
    return Array.isArray(item) ? 
        item.map((ele,index) => <span key={index}>{ele}</span>) 
        :
        item;
}

const Description = ({content, className}) => {
    return <div className={className}>
                {Array.isArray(content) ? 
                    content.map((item,index) =>  <p key={index} className={styles.describe}>{resolveArrayItem(item)}</p>)
                    :
                    <p className={styles.describe}>{content}</p>
                }
            </div>
}

export default Description;